.guide-article {
  max-width: 800px;
  margin: 2rem auto;
  margin-top: 1.5rem;
  padding: 2rem;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  border-radius: var(--border-radius);
  text-align: left;
  line-height: 1.6;
}

.article-header h1 {
  font-family: var(--font-heading);
  font-size: 2.8rem;
  font-weight: 700;
  color: var(--button-bg);
  margin-bottom: 0.8rem;
  position: relative;
  display: inline-block;
}

.article-header h1::after {
  content: "";
  display: block;
  width: 60%;
  height: 4px;
  background-color: var(--button-bg);
  margin: 8px auto 0;
  border-radius: 2px;
}

.article-subtitle {
  font-size: 1.2rem;
  color: var(--primary-text);
  font-weight: 500;
  max-width: 600px;
  margin: 0 auto;
  opacity: 0.9;
}

.article-content section {
  margin-bottom: 2rem;
}

.article-content h2 {
  font-family: 'Roboto Slab', serif;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: var(--button-bg);
  border-bottom: 2px solid var(--button-bg);
  padding-bottom: 0.5rem;
}

.article-content p {
  font-size: 1rem;
  margin-bottom: 1rem;
  text-align: justify;
}

.article-content ul {
  margin-left: 1.5rem;
  list-style-type: disc;
  margin-bottom: 1rem;
}

.article-content li {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.conclusion p {
  font-weight: bold;
  text-align: center;
}

/* MINDMAP */
.mindmap {
  background: #e8f0fe;
  border-radius: var(--border-radius);
  padding: 2rem;
  margin: 2rem 0;
  text-align: center;
}

.mindmap-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mindmap-node.main-node {
  background: #fff;
  border: 3px solid var(--button-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  width: 220px;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}

.mindmap-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  margin-top: 2rem;
}

.mindmap-node {
  background: #fff;
  border: 2px solid var(--button-bg);
  border-radius: var(--border-radius);
  padding: 1rem;
  text-align: center;
  font-size: 1rem;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  transition: transform var(--transition-speed) ease-in-out;
}

.mindmap-node:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}

/* Responsive */
@media (max-width: 768px) {
  .guide-article {
    padding: 1.5rem;
  }
  .article-header h1 {
    font-size: 2.4rem;
  }
  .article-content h2 {
    font-size: 1.8rem;
  }
  .article-content p,
  .article-content li {
    font-size: 0.95rem;
  }
  .mindmap-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .mindmap-grid {
    grid-template-columns: 1fr;
  }
}
