@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap');

:root {
  --body-bg: #F2F2F2;
  --header-bg: #002147;
  --primary-text: #333;
  --button-bg: #00539C;
  --button-hover: #007ACC;
  --button-active: #003D73;
  --button-text: #FFFFFF;
  --font-body: 'Montserrat', sans-serif;
  --border-radius: 12px;
  --transition-speed: 0.3s;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--body-bg);
  color: var(--primary-text);
  font-family: var(--font-body);
  text-align: center;
}

/* Header */
.ressources-header {
  background: #FFF;
  padding: 2rem 0;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.slogan {
  font-family: 'Roboto Slab', serif;
  font-size: 2.5rem;
  color: var(--button-text);
}

/* Conteneur des ressources */
.ressources-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  padding: 3rem 0;
  margin-top:12rem;
}

/* Cartes des ressources */
.ressource-card {
  background: #FFFFFF;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 320px;
  overflow: hidden;
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
  display: flex;
  flex-direction: column;
}

.ressource-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

/* Image */
.ressource-image img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

/* Contenu */
.ressource-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 1.5rem;
  text-align: left;
}

.ressource-content h3 {
  font-size: 1.4rem;
  color: var(--button-bg);
  margin-bottom: 0.5rem;
}

.ressource-content p {
  font-size: 1rem;
  color: var(--primary-text);
  flex-grow: 1;
}

/* Bouton Lire la suite */
.read-more {
  display: inline-block;
  background: var(--button-bg);
  color: var(--button-text);
  text-decoration: none;
  padding: 0.6rem 1rem;
  border-radius: var(--border-radius);
  transition: background var(--transition-speed);
  font-weight: bold;
  text-align: center;
  margin-top: 1rem;
}

.read-more:hover {
  background: var(--button-hover);
}

.read-more:active {
  background: var(--button-active);
}

/* Footer */
.ressources-footer {
  background: #162B44;
  color: var(--button-text);
  padding: 1rem 0;
  margin-top: 2rem;
}

.footer-container p {
  margin: 0;
  font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 768px) {
  .slogan {
    font-size: 2rem;
  }

  .ressources-container {
    flex-direction: column;
    align-items: center;
  }

  .ressource-card {
    width: 90%;
  }
}
